/* SP menu animation */
input#header-menu {
  display: none; }
  input#header-menu:checked ~ .header-nav {
    height: 100vh; }
  input#header-menu:checked + .header-icon .header-icon__detail {
    background: transparent; }
  input#header-menu:checked + .header-icon .header-icon__detail::before {
    transform: rotate(45deg); }
  input#header-menu:checked + .header-icon .header-icon__detail::after {
    transform: rotate(-45deg); }
  input#header-menu:checked + .header-icon .header-icon__detail::before,
  input#header-menu:checked + .header-icon .header-icon__detail::after {
    top: 0;
