/*
  ----------------
  Work page
  ----------------
*/

.work {
  @include padding(230px 0 0);
  @media #{$sp} {
    @include padding(120px 0 0);
  }

  &-ttlarea {
    margin-bottom: 50px;
    @media #{$sp} {
      margin-bottom: 40px;
    }
  }

  &-item {
    @include margin(30px 0 0);
    font-weight: bold;

    @media #{$sp} {
      line-height: 1.8;
      @include margin(30px 0 0);
    }
  }

  &-para {
    @include margin(10px 0 20px);
    @media #{$sp} {
      line-height: 1.8;
      @include margin(10px 0 20px);
    }
  }

  &-tools {
    display: flex;
    align-items: center;
    @media #{$sp} {
      flex-direction: column;
      align-items: flex-start;
    }

    &__ttl {
      font-weight: bold;
      margin-right: 25px;
      @media #{$sp} {
        margin-right: 0;
        margin-bottom: 8px;
      }
    }

    &__list {
      display: flex;
      align-items: center;
    }

    &__item {
      font-size: 14px;
      @media #{$sp} {
        font-size: 13px;
      }

      &:not(:last-of-type) {
        position: relative;
        margin-right: 24px;
        @media #{$sp} {
          margin-right: 20px;
        }

        &::after {
          content: "";
          display: block;
          position: absolute;
          width: 4px;
          height: 4px;
          background: #C4C4C4;
          border-radius: 50%;
          right: -15px;
          top: calc(50% -2px);
          @media #{$sp} {
            right: -11px;
          }
        }
      }
    }
  }
}

.work-dev {
  width: 100%;
  height: 490px;
  margin: 40px 0 100px;
  filter: grayscale(1);
  transition: all ease-in-out .3s;

  @media #{$sp} {
    height: 200px;
  }

  &:hover {
    @media #{$pc} {
      filter: grayscale(0);
    }
  }

  &--vary {
    background: url(../img/work/item-13-rgb.jpg) 50% / cover no-repeat;

    .work-dev__logo {
      width: 160px;
      height: 160px;

      @media #{$sp} {
        width: 100px;
        height: 100px;
      }
    }
  }

  &--mrt {
    background: url(../img/work/item-14-rgb.jpg) 50% / cover no-repeat;

    .work-dev__logo {
      width: 300px;
      height: 300px;

      @media #{$sp} {
        width: 160px;
        height: 160px;
      }
    }
  }

  &__link {
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
    transition: all ease-in-out .3s;

    &:hover {
      .work-dev__logo {
        @media #{$pc} {
          transform: scale(1.05);
        }
      }
    }
  }

  &__logo {
    position: relative;
    transition: all ease-in-out .3s;
  }
}

.work-grid {
  display: grid;
  grid-template-columns: 25% 25% 25% 25%;

  @media #{$pc} {
    -ms-grid-columns: 25% 25% 25% 25%;
    -ms-grid-rows: 250px 250px 250px;
    grid-template: 
      "a b c d"250px
      "e f g h"250px
      "i j k l"250px /
      25% 25% 25% 25%;
  }

  @media #{$sp} {
    grid-template-columns: 50% 50%;    
  }

  &__item {
    height: 250px;
    @media #{$sp} {
      height: 44.8vw;
    }

    &:nth-of-type(1) {
      @media #{$pc} {
        -ms-grid-row: 1;
        -ms-grid-column: 1;
        grid-area: a;
      }
      .work-grid__link {
        background: url(../img/work/item-01-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 160px;
        height: 160px;
      }

      @media #{$strictsp} {
        grid-area: 1/1/3/3;
        height: 89.6vw;
      }
    }

    &:nth-of-type(2) {
      @media #{$pc} {
        -ms-grid-row: 1;
        -ms-grid-column: 2;
        grid-area: b;
      }
      .work-grid__link {
        background: url(../img/work/item-02-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 150px;
        height: 150px;
        @media #{$strictsp} {
          width: 100px;
          height: 100px;
        }
      }
    }

    &:nth-of-type(3) {
      @media #{$pc} {
        -ms-grid-row: 1;
        -ms-grid-column: 3;
        grid-area: c;
      }
      .work-grid__link {
        background: url(../img/work/item-03-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 80px;
        height: 81px;
        @media #{$strictsp} {
          width: 54px;
          height: 54px;
        }
      }
    }

    &:nth-of-type(4) {
      @media #{$pc} {
        -ms-grid-row: 1;
        -ms-grid-column: 4;
        grid-area: d;
      }
      .work-grid__link {
        background: url(../img/work/item-04-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 110px;
        height: 115.5px;
        @media #{$strictsp} {
          width: 74px;
          height: 78px;
        }
      }
    }

    &:nth-of-type(5) {
      @media #{$pc} {
        -ms-grid-row: 2;
        -ms-grid-column: 1;
        grid-area: e;
      }
      .work-grid__link {
        background: url(../img/work/item-05-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 185px;
        height: 185px;
        @media #{$strictsp} {
          width: 134px;
          height: 134px;
        }
      }
    }

    &:nth-of-type(6) {
      @media #{$pc} {
        -ms-grid-row: 2;
        -ms-grid-column: 2;
        grid-area: f;
      }
      .work-grid__link {
        background: url(../img/work/item-06-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 150px;
        height: 49px;
      }

      @media #{$strictsp} {
        grid-area: 5/1/7/3;
        height: 89.6vw;
      }
    }

    &:nth-of-type(7) {
      @media #{$pc} {
        -ms-grid-row: 2;
        -ms-grid-column: 3;
        grid-area: g;
      }
      .work-grid__link {
        background: url(../img/work/item-07-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 180px;
        height: 180px;
        @media #{$strictsp} {
          width: 120px;
          height: 120px;
        }
      }
    }

    &:nth-of-type(8) {
      @media #{$pc} {
        -ms-grid-row: 2;
        -ms-grid-column: 4;
        grid-area: h;
      }
      .work-grid__link {
        background: url(../img/work/item-08-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 200px;
        height: 200px;
        @media #{$strictsp} {
          width: 134px;
          height: 134px;
        }
      }
    }

    &:nth-of-type(9) {
      @media #{$pc} {
        -ms-grid-row: 3;
        -ms-grid-column: 1;
        grid-area: i;
      }
      .work-grid__link {
        background: url(../img/work/item-09-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 140px;
        height: 140px;
        @media #{$strictsp} {
          width: 94px;
          height: 94px;
        }
      }
    }

    &:nth-of-type(10) {
      @media #{$pc} {
        -ms-grid-row: 3;
        -ms-grid-column: 2;
        grid-area: j;
      }
      .work-grid__link {
        background: url(../img/work/item-10-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 180px;
        height: 50px;
        @media #{$strictsp} {
          width: 120px;
          height: 33px;
        }
      }
    }

    &:nth-of-type(11) {
      @media #{$pc} {
        -ms-grid-row: 3;
        -ms-grid-column: 3;
        grid-area: k;
      }
      .work-grid__link {
        background: url(../img/work/item-11-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 150px;
        height: 150px;
        @media #{$strictsp} {
          width: 100px;
          height: 100px;
        }
      }
    }

    &:nth-of-type(12) {
      @media #{$pc} {
        -ms-grid-row: 3;
        -ms-grid-column: 4;
        grid-area: l;
      }
      .work-grid__link {
        background: url(../img/work/item-12-rgb.jpg) 50% / cover no-repeat;
      }

      .work-grid__logo {
        width: 145px;
        height: 145px;
        @media #{$strictsp} {
          width: 98px;
          height: 98px;
        }
      }
    }
  }

  &__link {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100%;
    filter: grayscale(1);
    transition: all ease-in-out .3s;

    &:hover {
      @media #{$pc} {
        filter: grayscale(0);
      }

      .work-grid__logo {
        @media #{$pc} {
          transform: scale(1.05);
        }
      }
    }
  }

  &__logo {
    position: relative;
    transition: all ease-in-out .3s;
  }
}


/* Work Detail */

.work-detail {
  @include padding(230px 0 0);
  @media #{$sp} {
    @include padding(120px 0 0);
  }

  &__container {
    display: flex;
    align-items: flex-start;
  }

  &__txtarea {
    width: 40%;
    @media #{$sp} {
      width: 100%;
    }
  }

  &__ttlimg {
    margin-bottom: 20px;
    @media #{$sp} {
      margin-bottom: 10px;
    }
  }

  &__link {
    position: relative;
    padding-left: 26px;
    margin-bottom: 40px;
    @media #{$sp} {
      font-size: 15px;
      padding-left: 20px;
      margin-bottom: 30px;
    }

    &:not(:first-of-type) {
      margin-left: 16px;
    }

    &::before {
      content: "";
      display: block;
      position: absolute;
      background: url(../img/work/icon-link.png) 50% / contain no-repeat;
      width: 18px;
      height: 18px;
      top: 2px;
      left: 0;
      @media #{$sp} {
        width: 16px;
        height: 16px;
        top: 2px;
      }
    }

    &--disable {
      z-index: -1;
      &::after {
        content: "";
        display: block;
        position: absolute;
        width: 100%;
        height: 100%;
        background: rgba(#fff, .8);
        top: 0;
        left: 0;
      }
    }
  }

  &__para {
    line-height: 1.8;
    margin-bottom: 10px;
  }

  &__role {
    color: #989898;
    margin-bottom: 40px;
    @media #{$sp} {
      margin-bottom: 25px;
    }
  }

  &__taglist {
    display: flex;
  }

  &__tagitem {
    color: #373535;
    background: #F8F8F8;
    font-size: 14px;
    padding: 4px 8px;
    border-radius: 5px;
    border: 1px solid #E0E0E0;

    &:not(:last-of-type) {
      font-size: 13px;
      margin-right: 10px;
    }
  }

  &__imgarea {
    display: flex;
    justify-content: flex-end;
    align-items: flex-start;
    width: 60%;
    @media #{$sp} {
      justify-content: center;
      width: 90vw;
      margin: 0 auto 30px;
    }

    img {
      display: inline-block;
      width: 90%;
      margin-top: -25px;
      @media #{$sp} {
        width: 100%;
        margin-top: 0;
      }
    }
  }
}